<template>
  <div>
    <div style="padding: 10px">
      {{msg}}
    </div>
    <div style="margin: 10px">
      <el-row>
            <el-button type="primary" @click="add">新增</el-button>
      </el-row>
    </div>

    <div style="margin: 10px">
      <el-table
          :data="tableData"
          stripe
          border
          style="width: 95%"
          size="medium">
        <el-table-column
            prop="userId"
            label="userId"
            width="150"
            sortable>
        </el-table-column>
        <el-table-column
            prop="username"
            label="用户名"
            width="120">
        </el-table-column>
        <el-table-column
            prop="area"
            label="地域"
            width="80">
        </el-table-column>
        <el-table-column
            prop="updateTime"
            label="创建时间"
            width="180"
            sortable>
        </el-table-column>
        <el-table-column
            prop="avatarUrl"
            label="头像"
            width="80"
            sortable>
        </el-table-column>
        <el-table-column
            prop="status"
            label="状态"
            width="80"
            sortable>
        </el-table-column>
        <el-table-column
            prop="email"
            label="邮箱"
            width="80"
            sortable>
        </el-table-column>
        <el-table-column
            prop="phone"
            label="手机"
            width="120"
            sortable>
        </el-table-column>
        <el-table-column
            fixed="right"
            label="操作"
        >
          <template #default="scope">

            <el-button @click="handleEdit(scope.row)" type="info" size="small">编辑</el-button>
            <el-popconfirm title="确定删除吗？" @confirm="handleDel(scope.row.userId)">
              <template #reference>
                <el-button  type="danger" size="small">删除</el-button>
              </template>
            </el-popconfirm>
          </template>
        </el-table-column>
      </el-table>
      <div style="margin: 10px">

        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="[5, 10, 20]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
        >
        </el-pagination>

        <el-dialog
            title="提示"
            v-model="dialogVisible"
            width="30%"
            :before-close="handleClose">
          <div style="margin: 20px;"></div>
          <el-form :label-position="labelPosition" label-width="80px" :model="formLabelAlign">
            <el-form-item label="username">
              <el-input v-model="formLabelAlign.username"></el-input>
            </el-form-item>
            <el-form-item label="phone">
              <el-input v-model="formLabelAlign.phone"></el-input>
            </el-form-item>
          </el-form>
          <template #footer>
            <span class="dialog-footer">
              <el-button @click="dialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="save">确 定</el-button>
            </span>
          </template>
        </el-dialog>
       </div>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "TXHYUser",
  data(){
    return{
      msg:"用户管理",
      dialogVisible:false,
      formLabelAlign:{},
      currentPage: 1,
      total:0,
      pageSize: 5,
      tableData: []
    }
  },
  created() {
    this.load();
  },
  methods: {
    load() {
      console.log(this)
      request.get("/txhy1", {
        params: {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
        }
      }).then(res => {
        console.log(res)
        this.tableData = res.data.users
        this.total = res.data.pageCount
      }).catch(function (err) {
        console.log(err)
      })
    },
    add() {
      this.dialogVisible = true
      this.formLabelAlign = {}
    },
    save() {
      if (this.formLabelAlign.userId) {
        request.put("/txhy1", this.formLabelAlign).then(res => {
          console.log(res)
          if (res.code === "0") {
            this.$message({
              type: "success",
              message: "更新成功"
            })
          } else {
            this.$message({
              type: "error",
              message: "更新失败"
            })
          }
          this.load()
          this.dialogVisible = false
        })
      } else {
        console.log(this.formLabelAlign.username)
        request.post("/txhy1",this.formLabelAlign)
            .then(res => {
          console.log(res)
          if (res.code === "0") {
            this.$message({
              type: "success",
              message: "添加成功"
            })
          } else {
            this.$message({
              type: "error",
              message: "添加失败"
            })
          }
          this.load()
          this.dialogVisible = false
        })
      }
    },
    handleEdit(row) {
      console.log(row);
      this.formLabelAlign=JSON.parse(JSON.stringify(row))
      this.dialogVisible=true
    },
    handleDel(row) {
      console.log(row);
      request.delete("/txhy1/"+row).then(res=>{
        console.log(res)
        if(res.code==="0"){
          this.$message({
            type:"success",
            message:"删除成功"
          })
        }else {
          this.$message({
            type:"error",
            message:"删除失败"
          })
        }
        this.load()
      })

    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.pageSize=val
      this.load()
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.currentPage=val
      this.load()
    }
  }
}

</script>

